<template>
  <div>
    <!-- Loading Overlay -->
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>
    
    <form  class="registration__form" @submit.prevent="updateProfile">
      <!-- Account info -->
      <div class="block__blackTitle">Account info</div>
      <div class="block__row nameRow">
  
        <multiselect class="nameRow__day"
                     :hideSelected = "true"
                     :show-labels="false"
                     placeholder="Title"
                     @input = "updateFields($event, 'title')"
                     :value="profile.fields.title"
                     :options="['Mr.', 'Ms.']"
                     v-if="profile.fields">
        </multiselect>
        <!-- first name-->
        <v-input name="Name"
                 :disabled="!profile.isFirstnameUpdatable"
                 type="text"
                 class="nameRow__fname"
                 @change="updateFields($event, 'firstname', 'isFirstnameUpdatable')"
                 v-model="profile.fields.firstname"
                 v-if="profile.fields">
        </v-input>
        <!-- Surname-->
        <v-input name="Surname"
                 :disabled="!profile.isSurnameUpdatable"
                 type="text"
                 @change="updateFields($event, 'surname', 'isSurnameUpdatable')"
                 v-model="profile.fields.surname"
                 v-if="profile.fields">
        </v-input>
      </div>
    
      <!-- Date of Birth -->
      <div class="block__blackTitle">Date of Birth</div>
      <div class="block__row dateRow">
        <multiselect class="dateRow__day"
                     :disabled="!profile.isBirthDateUpdatable"
                     :show-labels="false"
                     @input="updateBirthDate('birthDay', $event)"
                     :value="profile.fields.birthDay.toString().padStart(2, '0')"
                     :options="daysTab"
                     v-if="profile.fields">
        </multiselect>
        <multiselect class="dateRow__month"
                     :disabled="!profile.isBirthDateUpdatable"
                     :show-labels="false"
                     @input="updateBirthDate('birthMonth', $event)"
                     :value="profile.fields.birthMonth.toString().padStart(2, '0')"
                     :options="monthTab"
                     v-if="profile.fields">
        </multiselect>
        <multiselect class="dateRow__year"
                     :disabled="!profile.isBirthDateUpdatable"
                     :show-labels="false"
                     @input="updateBirthDate('birthYear', $event)"
                     :value="profile.fields.birthYear"
                     :options="yearsTab"
                     v-if="profile.fields">
        </multiselect>
      </div>
    
      <!-- Gender -->
      <div class="block__blackTitle">Gender</div>
      <div class="block__row checkBoxRow">
      
        <!--  Male -->
        <div class="checkbox">
          <input class="checkbox__input"
                 id="Male"
                 value="M"
                 v-model="profile.fields.gender"
                 type="radio"
                 @input="updateFields($event.target.value, 'gender')"
                 v-if="profile.fields">
          <label class="checkbox__label" for="Male">
                <span class="checkbox__text">
                  Male
                </span>
          </label>
        </div>
      
        <!--  Female -->
        <div class="checkbox">
          <input class="checkbox__input"
                 id="Female"
                 value="F"
                 v-model="profile.fields.gender"
                 type="radio"
                 @input="updateFields($event.target.value, 'gender')"
                 v-if="profile.fields">
          <label class="checkbox__label" for="Female">
                <span class="checkbox__text">
                  Female
                </span>
          </label>
        </div>
      </div>
    
      <div class="block__line"></div>
    
      <!-- Physical address -->
      <div class="block__blackTitle">Physical address</div>
    
      <div class="block__row phoneRow">

        <!-- Phone select  -->
        <multiselect :value="countries[phoneIndex]"
                     @input="updateFields($event.phonePrefix, 'mobilePrefix')" class="phoneRow__phoneCode"
                     placeholder="Code"
                     label="phonePrefix"
                     track-by="phonePrefix"
                     :options="countries"
                     :option-height="104"
                     :show-labels="false"
                     :hideSelected = "true"
                     v-if="profile.fields && phoneIndex !== ''">
          <template slot="singleLabel" slot-scope="props" class="select-line">
            <country-flag :country="props.option.code" size='small'/>
            {{props.option.phonePrefix}}
          </template>
          <template slot="option" slot-scope="props" class="select-line">
            <country-flag :country="props.option.code" size='small'/>
            {{props.option.phonePrefix}}
          </template>
        </multiselect>
      
        <!-- Phone number-->
        <v-input name="Phone number"
                 class="phoneRow__phone"
                 type="text"
                 @change="($event) => {updateFields($event, 'mobile'); delayTouch($v.profile.fields.mobile) }"
                 v-model="profile.fields.mobile"
                 v-if="profile.fields"
                 :isError="$v.profile.fields.mobile.$error"
        >
        <template slot="error">
          <div class="error" v-if="!$v.profile.fields.mobile.required">Field is required</div>
          <div class="error" v-else-if="!$v.profile.fields.mobile.type">Only numbers</div>
          <div class="error" v-else-if="!$v.profile.fields.mobile.minLength">Mobile should has more than {{ $v.profile.fields.mobile.$params.minLength.min }} symbols.</div>
        </template>
        </v-input>
    
      </div>
    
      <!-- Countries select  -->
      <div class="block__row countryRow">
      
        <!--  Country -->
        <multiselect class="countryRow__country"
                     @input="updateFields($event.code, 'country', 'isCountryUpdatable')"
                     :value="countries[countryIndex]"
                     :disabled="!profile.isCountryUpdatable"
                     placeholder="Select Country"
                     label="name"
                     track-by="name"
                     :options="countries"
                     :option-height="104"
                     :show-labels="true"
                     v-if="profile.fields && countryIndex !== ''">
          <template slot="singleLabel" slot-scope="props" class="select-line">
            <country-flag :country="props.option.code" size='small'/>
            {{props.option.name}}
          </template>
        
          <template slot="option" slot-scope="props" class="select-line">
            <country-flag :country="props.option.code" size='small'/>
            {{props.option.name}}
          </template>
        </multiselect>
        <!--  City  -->
        <v-input name="City"
                 class="countryRow__city"
                 type="text"
                 @change="($event) => {updateFields($event, 'city');  delayTouch($v.profile.fields.city)}"
                 v-model="profile.fields.city"
                 :isError="$v.profile.fields.city.$error"
                 v-if="profile.fields">
          <template slot="error">
            <div class="error" v-if="!$v.profile.fields.city.required">Field is required</div>
          </template>
          
        </v-input>
      </div>
    
      <!-- Address & Postal code -->
      <div class="block__row addressRow">
        <v-input name="Address"
                 type="text"
                 @change="($event) => {updateFields($event, 'address1'); delayTouch($v.profile.fields.address1)}"
                 v-model="profile.fields.address1"
                 :isError="$v.profile.fields.address1.$error"
                 v-if="profile.fields">
          <template slot="error">
            <div class="error" v-if="!$v.profile.fields.address1.required">Field is required</div>
          </template>

        </v-input>
        <v-input name="Postal code"
                 type="text"
                 @change="($event) => {updateFields($event, 'postalCode'); delayTouch($v.profile.fields.postalCode)}"
                 v-model="profile.fields.postalCode"
                 :isError="$v.profile.fields.postalCode.$error"
                 v-if="profile.fields">
          <template slot="error">
            <div class="error" v-if="!$v.profile.fields.postalCode.required">Field is required</div>
          </template>

        </v-input>
      </div>
    
      <div class="block__line"></div>
    
      <!--   Security     -->
      <div class="block__blackTitle">Security</div>
      <div class="block__row questionRow">
        <v-input name="Question"
                 class="questionRow__question"
                 type="text"
                 @change="($event) => {updateFields($event, 'securityQuestion'); delayTouch($v.profile.fields.securityQuestion)}"
                 v-model="profile.fields.securityQuestion"
                 :isError="$v.profile.fields.securityAnswer.$error"
                 v-if="profile.fields">
          <template slot="error">
            <div class="error" v-if="!$v.profile.fields.securityQuestion.required">Field is required</div>
          </template>
        </v-input>
        <v-input name="Answer"
                 class="questionRow__answer"
                 type="text"
                 @change="($event) => {updateFields($event, 'securityAnswer'); delayTouch($v.profile.fields.securityAnswer)}"
                 v-model="profile.fields.securityAnswer"
                 :isError="$v.profile.fields.securityAnswer.$error"
                 v-if="profile.fields">
          <template slot="error">
            <div class="error" v-if="!$v.profile.fields.securityAnswer.required">Field is required</div>
          </template>
        </v-input>
      </div>
    
      <!-- Submit -->
      <button class="btn btn--with-btn" type="submit" name="Create an account">
        Save info
        <span class="btn-in-btn">
            <img src="@/assets/img/svg/triangle.svg" alt="Arrow">
          </span>
      </button>
    </form>
  </div>
</template>

<script>
  const touchMap = new WeakMap();
  import Input from '@/components/Input';
  import CountryFlag from 'vue-country-flag';
  import Multiselect from 'vue-multiselect';
  import {numeric, required, minLength} from 'vuelidate/lib/validators';
  import {mapGetters, mapMutations, mapActions} from 'vuex';
  import call from '@/services/callService';
  
  
  export default {
    name: 'EditPersonalInfo',
    components: {
      'v-input': Input,
      'multiselect': Multiselect,
      'country-flag': CountryFlag,
    },
    data() {
      return {
        isLoading: false,
      }
    },
    validations: {
      profile: {
        fields: {
          mobile: {
            required,
            type: numeric,
            minLength: minLength(8),
          },
          city: { required },
          address1: { required },
          postalCode: { required },
          securityQuestion: { required },
          securityAnswer: { required }
        }
      }
    },
    methods: {
      ...mapMutations([
        'UPDATE_FIELDS',
        'UPDATE_BIRTH_DATE'
      ]),
      ...mapActions([
        'UPDATE_USER_PROFILE',
        'GET_USER_PROFILE',
        'GET_PROFILE_STATUS'
      ]),
    
      delayTouch($v) {
        $v.$reset();
        if (touchMap.has($v)) {
          clearTimeout(touchMap.get($v));
        }
        touchMap.set($v, setTimeout($v.$touch, 1000));
      },
      async updateProfile() {
        if (this.$v.$anyError) {
          this.$notify({
            group: 'app',
            type:'error',
            text: 'One or more fields have errors.'
          });
          
          return 0;
        }
        
        try {
          this.isLoading = true;
          let params = {
            "userID": this.profile.fields.userID,
            "username": this.profile.fields.username,
            "email": this.profile.fields.email,
            "country": this.profile.fields.country,
            "region": null,
            "personalID": this.profile.fields.personalID,
            "mobilePrefix": this.profile.fields.mobilePrefix,
            "mobile": this.profile.fields.mobile,
            "phonePrefix": this.profile.fields.mobilePrefix,
            "phone": this.profile.fields.mobile,
            "currency": this.profile.fields.currency,
            "title": this.profile.fields.title,
            "firstname": this.profile.fields.firstname,
            "surname": this.profile.fields.surname,
            "birthDate": this.profile.fields.birthDate,
            "city": this.profile.fields.city,
            "address1": this.profile.fields.address1,
            "address2": this.profile.fields.address2,
            "postalCode": this.profile.fields.postalCode,
            "language": this.profile.fields.language,
            "nationality": this.profile.fields.country, // check if need this field
            "birthplace": this.profile.fields.country,  // check if need this field
            "birthCountry": this.profile.fields.country, // check if need this field
            "securityQuestion": this.profile.fields.securityQuestion,
            "securityAnswer": this.profile.fields.securityAnswer,
            "acceptNewsEmail": this.profile.fields.acceptNewsEmail,
            "acceptSMSOffer": this.profile.fields.acceptSMSOffer,
            "gender": this.profile.fields.gender,
            "userConsents": {
              "termsandconditions": this.profile.fields.userConsents.termsandconditions,
              "emailmarketing": this.profile.fields.userConsents.termsandconditions,
              "sms": this.profile.fields.userConsents.termsandconditions,
              "3rdparty": this.profile.fields.userConsents.termsandconditions
            }
          };
          await call.accountUpdateProfile(params);
          this.UPDATE_USER_PROFILE();
          this.GET_PROFILE_STATUS();
          this.$notify({
            group: 'app',
            type:'success',
            text: 'Update profile completed successfully'
          });
        }
        catch (err) {
          const message = err.kwargs.desc || JSON.stringify(err);
          this.$notify({
            group: 'app',
            type:'error',
            text: message
          });
          console.error(`EM Update Profile Error: ${message}`);
        }
        finally {
          this.isLoading = false;
        }
      },
      updateFields(val, name, validation) {
        if (val === null) return 0;
        this.UPDATE_FIELDS({val, name, validation});
      },
      updateBirthDate(dateName, e) {
        if (e === null) return 0;
        let val = parseInt(e);
        this.UPDATE_BIRTH_DATE({val, dateName});
      },
      setPhonePrefix() {
       if (this.countries && this.countryIndex) {
         this.updateFields(this.countries[this.countryIndex].phonePrefix, 'mobilePrefix');
         this.updateFields(this.countries[this.countryIndex].phonePrefix, 'phonePrefix');
       }
      },
    },
    computed: {
      ...mapGetters({
        user: 'getUser',
        profile: 'getProfile',
        countries: 'getCountries',
        phonePrefixes: 'getPhonePrefixes'
      }),
      firstYear() {
        return (this.countries && this.countryIndex)
          ? new Date().getFullYear() - this.countries[this.countryIndex].legalAge
          : new Date().getFullYear() - 18;
      },
      daysTab() {
        let arr =[];
        for (let i = 1; i <= 31; i++) {
          arr.push(i.toString().padStart(2, '0'));
        }
        return arr;
      },
      monthTab() {
        let arr =[];
        for (let i = 1; i <= 12; i++) {
          arr.push(i.toString().padStart(2, '0'));
        }
        return arr;
      },
      yearsTab() {
        let arr = [];
        for (let i = this.firstYear; i >= this.firstYear - 100; i--) {
          arr.push(i);
        }
        return arr;
      },
      countryIndex() {
        return (this.countries && this.profile.fields)
          ? this.countries.map((e) => e.code).indexOf(this.profile.fields.country)
          : '';
      },
      phoneIndex() {
        if (this.countries && this.profile.fields && this.countryIndex) {
          this.setPhonePrefix();
          return (this.profile.fields.mobilePrefix)
            ? this.countries.map((e) => e.phonePrefix).indexOf(this.profile.fields.mobilePrefix)
            : this.countryIndex
        } else {
          return '';
        }
      }
    },
     mounted() {
       this.setPhonePrefix();
    }
  }
</script>

<style lang="sass" scoped>
  .nameRow, .addressRow
    justify-content: space-between
    .input
      width: calc(50% - 15px)
      
    .multiselect
      width: rem(100)
      margin-right: rem(15)
    &__fname
      margin-right: rem(15)
    
  .countryRow
    justify-content: space-between
    &__country, .input
      width: calc(50% - 10px)
  
  .questionRow
    &__question, .input
      width: calc(50% - 10px)
  
  .dateRow
    width: 100%
    justify-content: flex-start
    &__day, &__month
      width: 130px
      margin-right: 15px
    &__year
      width: 150px
  
  .checkBoxRow
    justify-content: flex-start
    .checkbox__label
      height: 42px
      width: 90px
      align-items: center
      margin-left: 10px
      &:before
        margin: 0
      &:after
        top: 13px
    
    .checkbox
      display: flex
      align-items: center
      border: 1px solid #eaeaea
      border-radius: 6px
      margin-right: 20px
      .checkbox__text
        margin: 0 0 0 8px
  
  .phoneRow
    justify-content: flex-start
    &__phoneCode
      width: 130px
      
      & /deep/ .multiselect__tags
        border-right: none
        border-bottom-right-radius: 0
        border-top-right-radius: 0
    &__phone
      width: calc(50% - 140px)
      & /deep/ .input__field
        border-left: none
        border-bottom-left-radius: 0
        border-top-left-radius: 0

</style>