<template>
  <div class="changePassword block">
    <div class="block__title">Change password</div>
    <div class="block__titleLine"></div>
  
    <!-- Loading Overlay -->
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>
    
    <form class="changepass__form" @submit.prevent="updatePass">
      <!-- Old password -->
      <vue-recaptcha
        v-if="isCaptchaEnabled"
        class="re-captcha"
        ref="recaptcha"
        :sitekey="sitekey"
        :loadRecaptchaScript="true"
        @verify="verify"
        @expired="onCaptchaExpired">
      </vue-recaptcha>
      
      <div class="block__row">
        <v-input  name="Old Password"
                  :type="typePassOld"
                  v-model="user.oldPassword"
                  @change="(val) => {user.oldPassword =  val}"
        >
          <template slot="optional">
            <button class="btn btn--empty" type="button" name="showPassOld" @click="showPassOld = !showPassOld">
              <img width="20" src="@/assets/img/svg/eye.svg" alt="eye" v-if="showPassOld">
              <img width="20" src="@/assets/img/svg/hide.svg" alt="eye" v-else>
            </button>
          </template>
        </v-input>
      </div>
      <div class="block__row">
      
      <create-password @onSend="updatePass"/>
      </div>
    </form>
  </div>
</template>

<script>
  import Input from '@/components/Input';
  import CreatePassword from '@/components/CreatePassword';
  import call from '@/services/callService';
  import VueRecaptcha from 'vue-recaptcha';
  import {mapGetters} from 'vuex';
  
  export default {
    name: 'change-password',
    components: {
      'v-input': Input,
      'create-password': CreatePassword,
      'vue-recaptcha' : VueRecaptcha,
    },
    data() {
      return {
        user: {
          oldPassword: '',
        },
        isLoading: false,
        isCaptchaEnabled: false,
        showPassOld: false,
        captchaResponse: null
      }
    },
    computed: {
      ...mapGetters({
        'sitekey' : 'getSitekey'
      }),
      typePassOld() {
        return this.showPassOld ? 'text' : 'password';
      }
    },
    methods: {
      needsCaptcha() {
        this.isLoading = false;
        this.isCaptchaEnabled = true;
        this.$notify({
          group: 'app',
          type: 'error',
          text: 'Password was not updated. Please, select captcha.'
        });
      },
      verify(recaptchaToken) {
        this.captchaResponse = recaptchaToken;
       
      },
      onCaptchaExpired() {
        this.$refs.recaptcha.reset();
      },
      updatePass(new_password) {
        const params = {
          'oldPassword': this.user.oldPassword,
          'newPassword': new_password,
        };
        if (this.isCaptchaEnabled && this.captchaResponse) {
          params.captchaPublicKey = this.sitekey;
          params.captchaResponse = this.captchaResponse;
        }
  
        if (!this.isLoading) {
          this.isLoading = true;
    
          call.changePassword(params)
            .then((result) => {
              
              if (result.kwargs.isCaptchaEnabled) {
                this.needsCaptcha()
              } else {
                this.isCaptchaEnabled = false;
  
                this.$notify({
                  group: 'app',
                  type:'success',
                  text: 'Password was updated'
                });
              }
            })
            .catch((err) => {
                  this.onCaptchaExpired();
                  const message = err.kwargs.desc || JSON.stringify(err);
                  this.$notify({
                    group: 'app',
                    type:'error',
                    text: message
                  });
                  console.error(`EM Create New Password Error: ${message}`);
            }).finally(() => {
            this.isLoading = false;
            });
        }
      }
    }
  }
</script>

<style lang="sass" scoped>
  .changePassword
    margin-top: 30px
  .changepass__form
    margin-top: 30px
  .input
    width: calc(50% - 15px)
  .create-pass__form
    width: calc(50% - 15px)
    /deep/.input
      width: 100%
    
</style>