<template>
  <div>
    <div class="accountInfo block">
      <div class="block__title">Personal information</div>
      <div class="block__titleLine"></div>

      <edit-personal-info v-if="status === 'edit'"/>
      <verify-account v-if="status === 'verify'"/>
      <compleated-profile v-if="status === 'done'"/>

    </div>
    <change-password/>
  </div>

</template>

<script>
  import ChangePassword from '@/components/Account/list/personalInfo/ChangePassword';
  import CompleatedProfile from '@/components/Account/list/personalInfo/CompleatedProfile';
  import EditPersonalInfo from '@/components/Account/list/personalInfo/EditPersonalInfo';
  import VerifyAccount from '@/components/Account/list/personalInfo/VerifyAccount';
  import {mapGetters} from 'vuex'
  
  export default {
    name: "PersonalInfo",
    components: {
      'change-password': ChangePassword,
      'compleated-profile' : CompleatedProfile,
      'edit-personal-info' : EditPersonalInfo,
      'verify-account' : VerifyAccount
    },
    computed: {
      ...mapGetters({
        'status' : 'getProfileStatus',
        'user' : 'getUser',
        'profile' : 'getProfile'
      })
    },
    methods: {}
  }
</script>

