<template>
    <div class="inputFile">
      <div v-if="title.length" class="inputFile__label">{{title}}</div>
      <div class="inputFile__layer">
        <div v-if="file.name" class="inputFile__name">{{file.name}}</div>
        <div v-else class="inputFile__empty">No file chosen</div>
        <div class="inputFile__clip"></div>
      </div>
      <input class="inputFile__field" type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
    </div>
</template>

<script>
  export default {
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data(){
      return {
        file: ''
      }
    },
    methods: {
      handleFileUpload(){
        this.file = this.$refs.file.files[0];
        this.$emit('fileUpload', this.file);
      },
    }
  }
</script>

<style lang="sass">
  .inputFile
    position: relative
    min-width: rem(220)
    &__layer
      position: relative
      border-radius: 0.375rem
      border: 0.063rem solid #e0e0e0
      background-color: #ffffff
      width: 100%
      padding: rem(10) rem(16)
      font-family: $mainFont
      font-size: 1rem
      font-weight: 400
      line-height: 1.5
      color: #636E7E
      display: flex
      justify-content: space-between
      align-items: center
    &__field
      position: absolute
      top: 0
      width: 100%
      height: 100%
      opacity: 0
    &__name
      color: $textColorMain
    &__clip
      width: 20px
      height: 18px
      background: url("../assets/img/account/clip.png")
    &__label
      font-size: 14px
      color: #636E7E
      margin-bottom: 3px

</style>