<template>
  <div class="completedInfo">
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>
    
    <div class="completedInfo__data">
      <div class="block__row">
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Name</div>
          <div class="completedInfo__value">{{profile.fields.firstname}} {{profile.fields.surname}}</div>
        </div>
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Date of Birth</div>
          <div class="completedInfo__value">{{profile.fields.birthDay}}.{{profile.fields.birthMonth}}.{{profile.fields.birthYear}}</div>
        </div>
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Gender</div>
          <div class="completedInfo__value">{{gender}}</div>
        </div>
      </div>
      
      <div class="block__row">
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Phone nubmer</div>
          <div class="completedInfo__value">{{profile.fields.mobile}}</div>
        </div>
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Country</div>
          <div class="completedInfo__value">{{ country(profile.fields.country) }}</div>
        </div>
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">City</div>
          <div class="completedInfo__value">{{profile.fields.city}}</div>
        </div>
      </div>
      
      <div class="block__row">
        <div class="completedInfo__dataItem completedInfo__dataItem-address">
          <div class="completedInfo__label">Adress</div>
          <div class="completedInfo__value">{{profile.fields.address1}}</div>
        </div>
        <div class="completedInfo__dataItem">
          <div class="completedInfo__label">Postal code</div>
          <div class="completedInfo__value">{{profile.fields.postalCode}}</div>
        </div>
      </div>
      
      </div>
    <div class="completedInfo__support">
      <div class="completedInfo__icon"></div>
      <div class="completedInfo__question">
        Need to change personal info?
      </div>
      <div class="completedInfo__label">
        Please, connect with our support
      </div>
      
      <button name="Support" class="btn btn__outline  completedInfo__btn">Support</button>
    
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';
  import call from '@/services/callService';
  import {mapActions} from 'vuex';

  export default {
    name: 'CompletedProfile',
    data() {
      return {
        isLoading: false
      }
    },
    async mounted() {
      try {
        this.isLoading = true;
        await this.GET_USER_PROFILE();
        this.isLoading = false;
      }
      catch (err) {
        const message = err.kwargs.desc || JSON.stringify(err);
        this.$notify({
          group: 'app',
          type:'error',
          text: message
        });
        console.error(`EM Get Profile Error: ${message}`);
      }
    },
    computed: {
      ...mapGetters({
        profile: 'getProfile',
        country: 'getFullCountryName'
      }),
      gender() {
        return this.profile.fields.gender === 'F' ? 'Female' : 'Male';
      },
    },
    methods: {
      ...mapActions([
        'GET_USER_PROFILE'
      ]),
      async updateProfile() {
        try {
          this.isLoading = true;
          await call.accountUpdateProfile(this.profile.fields);
          this.UPDATE_USER_PROFILE();
          this.$notify({
            group: 'app',
            type:'success',
            text: 'Update profile completed successfully'
          });
        }
        catch (err) {
          const message = err.kwargs.desc || JSON.stringify(err);
          this.$notify({
            group: 'app',
            type:'error',
            text: message
          });
          console.error(`EM Update Profile Error: ${message}`);
        }
        finally {
          this.isLoading = false;
        }
      },
    }
  }
</script>

<style lang="sass" scoped>
  .completedInfo
    display: flex
    justify-content: space-between
    margin-top: 30px
    &__data
      flex-grow: 1
    &__label
      font-size: 14px
      color: #636E7E
      margin-top: 5px
    &__dataItem
      width: calc(33% - 15px)
      &-address
        width: calc(66% - 15px)
    &__support
      width: 193px
      height: fit-content
      background: rgba(243, 244, 245, 0.1)
      border: 1px solid #EAEAEA
      border-radius: 16px
      padding: 20px
    &__icon
      width: 40px
      height: 40px
      background: url("../../../../assets/img/account/profile_support_info.png")
      margin-bottom: 10px
    &__btn
      margin-top: 30px

</style>